    /* Range Slider */

    
    #range-slider {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
          align-items: center;
      margin-left: 30px;
    }
    #range-slider #output {
      background: #359BCF;
      color: #fff;
      border-radius: 2px;
      padding: 3px 7px;
      margin: 0px 10px;
      text-align: center;
      position: relative;
    }
  #range-slider #output::before {
      content: '';
      position: absolute;
      left: -12px;
      top: 50%;
      -webkit-transform: translateY(-50%);
              -ms-transform: translateY(-50%);
          transform: translateY(-50%);
      height: 0;
      width: 0;
      border: solid 6px #359BCF;
      z-index: -1;
      border-top-color: white;
      border-bottom-color: white;
      border-left-color: white;
    }

    #message-box {
      width: 30%;
    }
    
    /* CHROME */
    #range {
      -webkit-appearance: none;
      outline: none;
      background: lightgray;
      height: 1px;
      width: 100%;
      border-radius: 5px;
    }
    #range::-webkit-slider-thumb {
      -webkit-appearance: none;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background: #359BCF;
      cursor: pointer;
    }
    
    /* FIREFOX */
    #range::-moz-range-thumb {
      border: none;
      height: 14px;
      width: 14px;
      border-radius: 50%;
      background: lightblue;
      cursor: grab;
    }
    
    #range::-moz-range-track {
      width: 100%;
      height: 3px;
      cursor: grab;
      background: lightblue;
      border-radius: 5px;
    }

 
 


